import React,{useContext} from 'react'
import CommentItem from './CommentItem'
import Store from './Store/context'

const CommentList = () => {
    // const list = [
    //     {userid:"web7722",content:"1",date:"2021-07-14"},
    //     {userid:"web7722",content:"2",date:"2021-07-14"},
    //     {userid:"web7722",content:"3",date:"2021-07-14"},
    //     {userid:"web7722",content:"4",date:"2021-07-14"},
    //     {userid:"web7722",content:"5",date:"2021-07-14"},
    //     {userid:"web7722",content:"6",date:"2021-07-14"},
    //     {userid:"web7722",content:"7",date:"2021-07-14"},
    // ]

    const {state} = useContext(Store) // {state,dispatch}
    const list = state.commentItem; 
    const {loadding,commentItem,error} = state





    const Item = list.map( (v,k)=>{        
        return (
            <CommentItem 
                key={k}
                userid={v.userid}
                content={v.content}
                date={v.date}
                index={v.id}
                location = {k}
            />
        )
    } )

    if(loadding) return <li>^^</li>
    if(error) return <li>!!</li>
    return (
        <li>
           {Item}
        </li>
    )
}

export default CommentList